.container {
  position: static;
}

.site-header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding: 7px 15px;
  z-index: 10;
  .logo {
    margin-top: 5px;
    float: left;
    width: 140px;
    height: 45px;
    overflow: hidden;
    img {
      width: 100%;
      height: auto;
      -webkit-filter: brightness(0);
      filter: brightness(0);
      -moz-filter: brightness(0)
    }
  }
  .main-nav {
    float: right;
    .nav-btn {
      float: right;
      background: transparent;
      margin: 20px 15px;
      border: none;
      font-size: 25px;
      color: #fff;
      transition: 0.3s all ease-out;
      z-index: 10;
      &:hover {
        color: $brand-color;
      }
      &:focus {
        outline: none;
      }
    }
    .remove-btn {
      display: none;
    }
    .navigation {
      display: none;
      position: absolute;
      width: 100%;
      height: 100vh;
      text-align: center;
      background-color: rgba(0, 0, 0, 0.9);
      top: 0;
      left: 0;
      padding: 20vh 0 0;
      z-index: -1;
      li.current-menu-item a {
        text-decoration: underline;
        color: $brand-color;
      }
      li {
        margin: 5vh 0;
      }
      a {
        font: 700 16px $lato;
        color: #fff;
        transition: 0.3s all ease-out;
        &:hover {
          color: $brand-color;
        }
      }
    }
  }

  .sign-in {
    margin-top: 22px;
    margin-right: 15px;
    display: block;
    float: right;
    color: #fff;
    font-size: 20px;
    &:hover {
      color: $brand-color;
    }
  }
  .login-wrap {
    display: inline-block;
    margin-top: 22px;
    color: #fff;
    font-size: 20px;
    .sign-out {
      float: right;
      margin-left: 10px;
      color: #fff;
      font-size: 20px;
      &:hover {
        color: $brand-color;
      }
    }
  }
  .username {
    float: left;
    margin-left: 10px;
    font: 700 16px $lato;
    color: #fff;
    transition: 0.3s all ease-out;
    &:hover {
      color: $brand-color;
    }
  }
  .logged-in .sign-in {
    display: none;
  }

}

.header-page {
  position: fixed;
  background-color: rgba(45,38,32,0.9);
}

.fixed-scroll {
  width: 100%;
  position: fixed;
  background-color: rgba(45,38,32,0.9);
  transition: 0.5s;
  z-index: 99;
}

.login {
  display: none;
}

.logged-in .sign-out {
  display: block;
}

.logged-in .username {
  display: block;
}

.logged-in .sign-in {
  display: none;
}

@media (min-width: 768px) {
  .site-header {
    .login-wrap {
      margin-top: auto;
    }
    .main-nav {
      .nav-btn {
        display: none;
      }
      .navigation {
        float: right;
        display: flex;
        position: relative;
        width: auto;
        height: auto;
        background-color: transparent;
        top: auto;
        left: auto;
        padding: 25px 0;
        z-index: 1;
        li {
          margin: 0;
          padding: 0 9px;
          &:last-child {
            padding-right: 0;
          }
          &:first-child {
            padding-left: 0;
          }
        }
      }
    }
    .sign-in,
    .sign-out {
      padding: 25px 0;
    }
    .sign-in {
      margin: 0 10px;
    }
    .username {
     padding: 25px 0;
    }

    .login-wrap {
      float: right;
    }
  }

  @media (min-width: 992px) {
    .site-header {
      .main-nav {
        .navigation {
          li {
            padding: 0 25px;
          }
        }
        .login {
          margin-left: 50px;
        }
        .sign-out {
          margin-left: 10px;
        }
      }
    }
  }
}
